import $C from './config.js';
import $store from '@/store/index.js';
export default {
    formatImg(url) {
        if (url == null || url.indexOf("http:") != -1 || url.indexOf("https:") != -1) {
            return url;
        } else {
            if (uni.conf && uni.conf.cdnUrl) {
                return uni.conf.cdnUrl + url;
            } else {
                $store.dispatch('initConf');
                return url;
            }
        }
    },
    getLocalTimestamp() {
        return parseInt(new Date().getTime() / 1000);
    },
    getVideoBufferTime(){
		try{
			if (uni.conf && uni.conf.videoBuffertime) {
			    return parseInt(uni.conf.videoBuffertime);
			} 
		}catch(e){}
		return 0;
	},
	/**
	 * 判断是否为空
	 */
	isEmpty(value) {
		switch (typeof value) {
			case 'undefined':
				return true;
			case 'string':
				if (value.replace(/(^[ \t\n\r]*)|([ \t\n\r]*$)/g, '').length == 0) return true;
				break;
			case 'boolean':
				if (!value) return true;
				break;
			case 'number':
				if (isNaN(value)) return true;
				break;
			case 'object':
				if (null === value || value.length === 0) return true;
				for (var i in value) {
					return false;
				}
				return true;
		}
		return false;
	},
	navigateBack(){
		uni.navigateBack()
	},






























































































}
